/* override bootstrap styles */
@import url("http://fonts.googleapis.com/css?family=PT+Sans:400,400italic,700&subset=latin,cyrillic");

html {
  position: relative;
  min-height: 100%;
}

body {
  font-family:'PT Sans',arial narrow,sans-serif;
  padding-top:50px;
  color:#646464;
  background-color:#efefef;
  margin-bottom: 60px;
}

#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: 60px;
  padding-top:20px;
  padding-bottom:40px;
  background-color: #cecece;
}

.label {
  a {
    color: white;
    text-decoration: underline;
  }
}

form, .form-control, .btn{
  letter-spacing: 0.1em;
  font-size: 16px;
  font-weight: 400;
}

// footer {
//   margin-top:40px;
//   padding-top:40px;
//   padding-bottom:40px;
//   background-color:#cecece;
// }

#search{
  input {
    font-size: 14px;
  }
  button {
    font-size: 14px;
  }
}

.desc {
  font-size: 16px;
  letter-spacing: 0.1em;
  margin-top: 10px;
}

#masthead {
  min-height:199px;
  background:url(../images/bg.jpg) no-repeat center;
  background-size: cover;
  color:#aaaacc;

  .input-group {
    margin-top: 22px;
  }

  h3 {
    font-size: 24px;
    line-height: 1;
    color:#fefeff;
    margin-top:50px;
  }

  .well {
    margin-top:31px;
    background-color:rgba(1,1,1,0.3);
    color:#efefff;
    min-height:127px;
  }

  .well-lg {
    border: none;
  }
}

#topPanel {
  padding-top:0px;
  padding-left:22px;
  padding-right:22px;

  .panel-body {
    padding-top:0px;
  }

}

.navbar.affix {
  position:fixed;
  top:0;
  width:100%;
}

.top-spacer {
  background-color:#ffffff;
  height:40px;
}

a,a:hover {
  color:#223344;
  text-decoration:none;
}

.icon-bar {
  background-color:#fff;
}

.navbar-bright {
  background-color:#333;
  color:#fff;
  margin-bottom: 0;
  border-width: 0;
}

.navbar-bright a, .navbar-bright .navbar-brand, #masthead h4 {
  color:#fff;
  font-size:16px;
  text-transform: uppercase;
  letter-spacing:.08em;
}

.navbar-bright li > a:hover {
  background-color:#337ab7;
}

.dropdown-menu {
  min-width: 250px;
}

.caret {
  color:#fff;
}

.navbar-toggle {
  color:#fff;
  border-width:0;
}

.navbar-toggle:hover {
  background-color:#666;
}

.panel {
  border-color:transparent;
  border-radius:0;
}

.thumbnail {
  margin-bottom:8px;
}

.img-container {
  overflow:hidden;
  height:170px;
}

.img-container img {
  min-width:280px;
  min-height:180px;
  max-width:380px;
  max-height:280px;
}

.txt-container {
  overflow:hidden;
  height:100px;
}

.panel .lead {
  overflow:hidden;
  height:90px;
}

.label-float{
  margin:0 auto;
  position: absolute;
  top: 0;
  z-index: 1;
  width:100%;
  opacity:.9;
  padding:6px;
  color:#fff;
}

@media screen and (min-width: 768px)
{
  .navbar-nav {
    margin-bottom: -5px;
  }
}

.navbar .navbar-nav {
  display: inline-block;
  float: none;
}

.navbar .navbar-collapse {
  text-align: center;
}

h3#date {
  margin-top: 0;
}

.btn-twitter {background-color:#4099FF;color:#fff;}
.btn-facebook {background-color:#3B5998;color:#fff;}
.btn-google-plus {background-color:#d34836;color:#fff;}

.btn-twitter:hover {background-color:#3088FF;color:#62BBFF;}
.btn-facebook:hover {background-color:#2A4998;color:#4C6BBA;}
.btn-google-plus:hover {background-color:#C23836;color:#E45A58;}
